    body {
        background-image: url("E:\Web Development\Projects\ReactNotesApp\notekaro\public\MOT244-Deep-Lavender-Handmade-paper.webp");
        margin: 0;
        background-size: cover;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    
    code {
        font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    }
    
    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    .dark-mode {
        /*background-color: black;*/
        background-image: url("E:\Web Development\Projects\ReactNotesApp\notekaro\public\black-paper-texture.webp");
        margin: 0;
        background-size: cover;
    }
    
    .dark-mode h1 {
        color: white;
    }
    
    .container {
        max-width: 960px;
        margin-right: auto;
        margin-left: auto;
        padding-right: 15px;
        padding-left: 15px;
        min-height: 100vh;
    }
    
    .notes-list {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: repeat( auto-fill, minmax(250px, 1fr));
    }
    
    .note.new {
        background-color: #67d7cc;
    }
    
    textarea {
        border: none;
        resize: none;
        background-color: #67d7cc;
    }
    
    textarea:focus {
        outline: none;
    }
    
    .save {
        background-color: #e1e1e1;
        border: none;
        border-radius: 15px;
        padding: 5px 10px 5px 10px;
    }
    
    .save:hover {
        background-color: #ededed;
        cursor: pointer;
    }
    
    .note {
        /*background-image: url("E:\Web Development\Projects\ReactNotesApp\notekaro\public\0111-1-512x512.webp");*/
        background-color: #e1e1e1;
        border-radius: 10px;
        padding: 1rem;
        min-height: 170px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        white-space: pre-wrap;
    }
    
    .note-footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    .delete-icon {
        cursor: pointer;
    }
    
    .search {
        display: flex;
        align-items: center;
        background-color: rgb(233, 233, 233);
        border-radius: 10px;
        padding: 5px;
        margin-bottom: 1.5em;
    }
    
    .search input {
        border: none;
        background-color: rgb(233, 233, 233);
        width: 100%;
    }
    
    .search input:focus {
        outline: none;
    }
    
    h1 {
        color: white;
        margin-left: auto;
        margin-right: auto;
        font-size: 55px;
    }